import { IAppUser } from '../../services/account';

export const LOGIN_REQUEST = 'LOGIN_REQUEST';
export const LOGIN_FAILURE = 'LOGIN_FAILURE';

export const CHANGE_PASSWORD_REQUEST = 'CHANGE_PASSWORD_REQUEST';
export const CHANGE_PASSWORD_FAILURE = 'CHANGE_PASSWORD_FAILURE';

export const VERIFY_REQUEST = 'VERIFY_REQUEST';
export const VERIFY_SUCCESS = 'VERIFY_SUCCESS';
export const VERIFY_FAILURE = 'VERIFY_FAILURE';

export const LOGOUT = 'LOGOUT';

interface LoginRequestAction {
  type: typeof LOGIN_REQUEST,
}

interface LoginFailureAction {
  type: typeof LOGIN_FAILURE,
  error: string,
}

interface ChangePasswordRequestAction {
  type: typeof CHANGE_PASSWORD_REQUEST,
}

interface ChangePasswordFailureAction {
  type: typeof CHANGE_PASSWORD_FAILURE,
  error: string,
}

interface VerifyRequestAction {
  type: typeof VERIFY_REQUEST,
}

interface VerifySuccessAction {
  type: typeof VERIFY_SUCCESS,
  payload: IAppUser,
}

interface VerifyFailureAction {
  type: typeof VERIFY_FAILURE,
}

interface LogoutAction {
  type: typeof LOGOUT,
}

export type Actions =
  LoginRequestAction |
  LoginFailureAction |
  ChangePasswordRequestAction |
  ChangePasswordFailureAction |
  VerifyRequestAction |
  VerifySuccessAction |
  VerifyFailureAction |
  LogoutAction;

const LOGIN_FAIL = 'Thng tin ng nhp khng chnh xc! Vui lng th li hoc chc chn rng phm CAPSLOCK ang khng bt.';
const PASSWORD_NOT_MATCH = 'Mt khu xc nhn khng ng!';
const PASSWORD_NOT_CHANGE = 'Mt khu c khng c trng vi mt khu mi!';
const PASSWORD_CHANGE_FAILURE = 'Khng th i mt khu do thng tin nhp khng chnh xc!';

export const authErrors = {
  LOGIN_FAIL,
  PASSWORD_NOT_MATCH,
  PASSWORD_NOT_CHANGE,
  PASSWORD_CHANGE_FAILURE,
};
